@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,300&display=swap");

* {
  box-sizing: border-box;
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  animation: fade-in 2s;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* header  */
header {
  padding: 30px;
  text-align: center;
}

.heading {
  font-family: "Caveat", cursive;
  font-size: 3.5rem;
  color: palevioletred;
  font-weight: 700;
  text-shadow: 1px 1px 3px #bbb;
}

/* main  */
main {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

/* form  */
input {
  width: 50vw;
  height: 7vh;
  text-align: center;
  border: 1px solid #bbb;
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
  box-shadow: 3px 0 3px #bbb, 0 3px 3px #bbb, -1px 0 3px #bbb, 0 -1px 3px #bbb;
}

#add-todo-button {
  display: none;
}

/* list  */
.list {
  list-style: none;
  width: 50vw;
  margin: 0;
  padding: 0;
}

.list-item {
  display: flex;
  align-items: center;
  max-height: fit-content;
  min-height: 8vh;
  word-wrap: break-word;
  background: #f5f5f5;
  border: 1px solid #bbb;
  box-shadow: 3px 0 3px #bbb, -1px 0 3px #bbb;
  cursor: pointer;
}

#clear {
  min-height: 5vh;
  background: #eee;
  box-shadow: 3px 0 3px #bbb, 0 3px 3px #bbb, -1px 0 3px #bbb;
  display: flex;
  justify-content: flex-end;
  font-family: "Roboto", sans-serif;
}

.clear-completed {
  text-decoration: underline;
  font-size: 1rem;
  color: #444;
  margin: 0 10px;
  cursor: pointer;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

#mark-complete {
  margin: 0 10px;
}

.content {
  margin: 0;
  margin-right: auto;
  padding: 5px 0;
  font-size: 1.5rem;
  color: #444;
  word-wrap: break-word;
  font-family: "Roboto", sans-serif;
  font-style: italic;
}

.completed {
  color: #999;
  text-decoration: line-through;
}

.darken {
  background: #eee;
}

#mark-delete {
  margin-left: auto;
  margin-right: 10px;
}

/* footer  */
footer {
  font-family: "Caveat", cursive;
  margin: 50px;
  text-align: center;
  color: #999;
  font-size: 1.5rem;
  font-weight: 700;
}

#heart {
  color: palevioletred;
}

/* media queries  */
@media screen and (max-width: 500px) {
  /* header */
  .heading {
    font-size: 2rem;
  }

  /* form  */
  form {
    width: 80vw;
    display: flex;
  }

  /* input  */
  input {
    width: 80vw;
    height: 7vh;
    font-size: 1rem;
  }

  /* list  */
  .list {
    width: 80vw;
  }

  .list-item {
    height: fit-content;
    padding: 0;
  }

  .content {
    font-size: 1.2rem;
  }

  .clear-completed {
    font-size: 0.8rem;
  }

  /* footer  */
  footer {
    font-size: 0.9rem;
  }
}
